import {NavLink, Outlet} from "react-router-dom";
import {Button, Container, Nav, Navbar} from "react-bootstrap";
import {useDispatch, useSelector} from "react-redux";
import {logout} from "../reducers/userReducer";
import logo from "../img/logo-krug-gold.png";
const Layout = () =>{

    const isAuth = useSelector(state => state.user.isAuth);
    const isAdmin = useSelector(state => state.user.currentUser.role) === "admin";
    const dispatch = useDispatch();

    return(
        <>
            <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark" style={{fontSize: "1.1em",padding: "15px"}}>
                {/*<Container>*/}
                    {/*<header className={"App-header"}>*/}
                    <Navbar.Brand>
                        <NavLink to={"/"}><img src={logo} width={"60px"} height={"60px"}/></NavLink>
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav"/>

                    <Navbar.Collapse  id="responsive-navbar-nav">
                        <Nav className="me-auto">
                            <NavLink to={"/news"}></NavLink>
                            <NavLink to={"/lessons"}></NavLink>
                            <NavLink to={"/materials"}></NavLink>
                            <NavLink to={"/notes"}></NavLink>
                            {isAdmin &&<NavLink to={"/listUsers"}></NavLink>}

                        </Nav>
                        <Nav>
                            {/*<div className={"auth"}>*/}
                            {!isAuth && <NavLink to={"/login"}></NavLink>}
                            {!isAuth && <NavLink to={"/registration"}></NavLink>}
                                {isAuth && <div style={{color:"white"}}>{localStorage.getItem('email')}</div>}
                                {isAuth && <Button className={"logOut"} onClick={() => dispatch(logout())}></Button>}
                            {/*</div>*/}
                        </Nav>
                    </Navbar.Collapse>

                    {/*</header>*/}
                {/*</Container>*/}
            </Navbar>
            <main className={"content"}>
                <Outlet />
            </main>

        </>
    )
};
export {Layout};
